<template>
    <div id="FormComponent">
        <h2>Form component</h2>
        <form>
            <label for="txtNombre">Nombre: </label>
            <input type="text" id="txtNombre" v-model="title"><br>
            <label for="txtComentario">Comentario:</label>
            <textarea id="txtComentario" v-model="body"></textarea><br>
            <input type="submit" v-on:click="addComment" value="Enviar">
        </form>
        <p>{{  title  }}</p>
        <p>{{  body }}</p>
        <p>origen evento: {{  evento }}</p>
        <p>tipo evento: {{  tipo}}</p>

        <div id="divEspecial" v-on:mouseover="verDatosMouseover">

        </div>
        <p>origen evento: {{  eventoMouseover }}</p>
        <p>tipo evento: {{  eventoMouseoverTipo }}</p>
    </div>
</template>

<script>
export default{
    name: 'FormComponent',
    props: [],
    data: function(){
        return{
            title: '',
            body:'',
            evento: '',
            tipo: '',
            eventoMouseover:'',
            eventoMouseoverTipo: '',

        }
    },
    methods:{
        addComment:function(event){
            console.log(event);
            this.evento = event.target.tagName;
            this.tipo = event.type;
            event.preventDefault();
        },
        verDatosMouseover:function(event){
            console.log(event);
            this.eventoMouseover= event.target.tagName;
            this.eventoMouseoverTipo = event.type;
        }
    },

}
</script>


<style scoped>
#FormComponent{
    background-color: bisque;
    margin: 2%;
}

#divEspecial{
    width: 200px;
    height: 200px;
    background-color: blueviolet;
}
</style>